body{
  background-color: #2e2e3a;
  font-family: Arial, Helvetica, sans-serif;
  padding: 60px 0;
  display: flex;
  justify-content: space-around;

}
p{
  margin: 0;
}

#root{
  width:317px;
  border-radius: 15px;
  background-color: #2c2d31;
  color: rgb(216, 206, 206);
  box-shadow:inset 0 325px 12px rgba(0, 0, 0, 0.7);
  
}

.profile{
  width:100%;
  border-radius: 15px 15px 0 0; 
}

.Header{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Header h2{
  margin: 25px 0 0 0;
}

.Header h5{
  margin: 3px;
  color: #F3BF99 ;
}

.Header p{
  font-size: 14px;
}
.social-button{
  display: flex;
  justify-content: space-evenly;
  margin: 10px 10px 25px 10px;
}

button{
  display: flex;
  align-items: center;
  padding: 8px 25px;
  border-radius: 8px;
  border: none;
  box-shadow:  -2px 10px 15px rgba(0, 0, 0, 1);
  transition: all 0.5s;
  text-decoration: none;
}
button:active{
  box-shadow: 0 0 0 rgba(0, 0, 0, 0.0);
}
 button img{
  width:16px;
  margin: 0 10px 0 0;
}

.email{
  background-color: white;
}
.linkedin{
  background-color: #5093E2;
  color:white;
}

.Contents{
  padding:20px 28px 0 28px;
}

.Contents h3{
  margin: 0px 0 10px 0;
}
.Contents p{
  font-size: 13px;
  line-height: 16px;
  margin-bottom: 25px;
}

.Footer{
  display: flex;
  justify-content: space-between;
  padding: 0px 18px 35px 18px;
  border-radius: 0 0 15px 15px;
  box-shadow: inset 0px -4px 5px rgba(0, 0, 0, 0.3);
}

a:link {
  text-decoration: none;
}